export function english(Text) {
    return Text;
}

export function chinese(Text) {
    return ({
        'Issue Page': '',
        'Initialize Comments': '',
        'Loading comments...': '...',
        'Error: Comments Not Initialized': '()',
        'No Comment Yet': '()',
        'Previous': '',
        'Next': '',
        'Nothing to preview': '',
        'Loading preview...': '...',
        'Submitting...': '...',
        'Comment': '',
        'Write': '',
        'Preview': '',
        'Logging in...': '...',
        'Login to Comment': '',
        'Leave a comment': '()',
        'Login': '',
        'Logout': ''
    }[Text] || Text);
}

export function chineseTraditional(Text) {
    return ({
        'Issue Page': '',
        'Initialize Comments': '',
        'Loading comments...': '...',
        'Error: Comments Not Initialized': '()',
        'No Comment Yet': '()',
        'Previous': '',
        'Next': '',
        'Nothing to preview': '',
        'Loading preview...': '...',
        'Submitting...': '...',
        'Comment': '',
        'Write': '',
        'Preview': '',
        'Logging in...': '...',
        'Login to Comment': '',
        'Leave a comment': '()',
        'Login': '',
        'Logout': ''
    }[Text] || Text);
}

// @see:
//  https://www.w3.org/TR/1999/REC-html401-19991224/struct/dirlang.html#h-8.1.1
//  https://gist.github.com/JamieMason/3748498
export function fromLanguageCode(code) {
    const culture = /^([^-]+)-/
    return ({
        'en': english,
        'zh': chinese,
        'zh-CN': chinese,
        'zh-CHS': chinese,
        'zh-TW': chineseTraditional, 
        'zh-HK': chineseTraditional,
        'zh-CHT': chineseTraditional,
        'en-US': english
    }[code] || (culture.test(code) && fromLanguageCode(code.match(culture)[1])) || english);
}

export default english;


